.buttonReset {
  cursor: pointer;
  color: #000;
  text-align: center;
  letter-spacing: .03em;
  background-color: wheat;
  border: none;
  border-radius: 4px;
  margin-left: 150px;
  padding: 6px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.62;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.buttonReset:hover, .buttonReset:focus {
  color: wheat;
  background-color: #000;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

